   export async function GET(req, {params}){
    const { id } = params;
    // Logic to fetch user by id
    const user = await GET(id);
    if (!user) {
        return new Response("User not found", { status: 404 });
    }
    return new Response(JSON.stringify(user), { status: 200 });
}